            <a href="#" class="social-link wechat-link" title="微信"></a>
            <a href="#" class="social-link zhihu-link" title="知乎"></a>
          </div>
        </div>
      </div>
      
      <div class="contact-form-section animate__animated animate__fadeInRight">
        <h2>发送消息</h2>
        <el-form :model="formData" :rules="rules" ref="contactForm" label-position="top">
          <el-form-item label="姓名" prop="name">
            <el-input v-model="formData.name" placeholder="请输入您的姓名"></el-input>
          </el-form-item>
          
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="formData.email" placeholder="请输入您的邮箱"></el-input>
          </el-form-item>
          
          <el-form-item label="主题" prop="subject">
            <el-input v-model="formData.subject" placeholder="请输入消息主题"></el-input>
          </el-form-item>
          
          <el-form-item label="消息内容" prop="message">
            <el-input 
              v-model="formData.message" 
              type="textarea" 
              :rows="5" 
              placeholder="请输入您的消息内容"
            ></el-input>
          </el-form-item>
          
          <el-form-item>
            <el-button type="primary" @click="submitForm" :loading="submitting">发送消息</el-button>
            <el-button @click="resetForm">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
